.payment-radio-wrapper {
  min-height: remCalc(89);

  @include media-breakpoint-up(sm) {
    .payment-radio {
      min-width: remCalc(200);
    }
  }

  .payment-description-icon {
    width: remCalc(20);
    height: remCalc(20);
    cursor: pointer;
    &:hover {
      text-decoration: none;
    }
  }

  .payment-description-icon-placeholder {
    width: remCalc(20);
  }
}