
ff-record-list {

  @include media-breakpoint-only(xs) {
    ff-record:nth-child(even) {
      padding-left: remCalc(8);
    }

    ff-record:nth-child(odd) {
      padding-right: remCalc(8);
    }
  }

  @include media-breakpoint-only(sm) {
    ff-record:nth-child(even) {
      padding-left: remCalc(8);
    }

    ff-record:nth-child(odd) {
      padding-right: remCalc(8);
    }
  }

  ff-record {

    .producttile {
      cursor: pointer;
    }

    .thumb {
      background-size: cover;

      //150% von der width, um da wo das Bild ist von vorherein ein 2 zu 3 Bereich aufzuspannen
      padding-bottom: 150%;
    }

    .details {
      .name:hover, .name:focus, .name:active {
        text-decoration: none;
      }
    }

    .colortiles {

      .colortiles-container {

        .colortile {
          width: remCalc(18);
          height: remCalc(18);
          border: 1px solid $gray-line;
        }

        .colortile:first-child {
          width: remCalc(12);
          height: remCalc(12);

          //important muss hier sein um bootstrap margin zu berschreiben, was auch schon important ist
          margin-left: remCalc(1) !important;

          &:after {
            content: '';
            position: relative;
            @extend .rounded-circle;
            border: 1px solid $gray-primary;
            display: block;
            height: remCalc(18);
            width: remCalc(18);
            top: remCalc(-4);
            left: remCalc(-4);
          }
        }
      }
    }
  }
}


