.form-control {
  height: auto;
}

.custom-select {
  background: none;
  @include getIcon("chevronDown", $gray-primary, 25);
  background-repeat: no-repeat;
  background-position: calc(100% - 0.7rem) 50%;
  width: 100%;
  height: auto;
  &::-ms-expand {
    display: none;
  }
}

.invalid-feedback {
  position: relative;
  &::before {
    @include getIcon("close", $white, 16);
    background-color: $red;
    background-position: 50% 50%;
    border-radius: 50%;
    content: "";
    left: 0;
    top: remCalc(4);
    position: absolute;
  }
}

.is-invalid {
  .form-control {
    border-color: $red;
  }
  .invalid-feedback {
    display: block;
  }
}

.custom-checkbox {
  .custom-control-input {
    ~ .custom-control-label {
      &::before {
        top: remCalc(4);
      }
    }
    &:checked ~ .custom-control-label {
      &::after {
        @include getIcon("checkmarkSmall", $gray-primary, 45);
        background-position: 50% 50%;
        background-size: contain;
        width: remCalc(16);
        height: remCalc(16);
        top: remCalc(4);
      }
    }
    &:disabled ~ .custom-control-label {
      color: $gray-dark;
      &::before {
        background-color: $white;
        border-color: $gray-dark;
        top: remCalc(4);
      }
    }
  }
}

.custom-checkbox.custom-checkbox--round,
.custom-radio.custom-radio--round {
  .custom-control-input ~ .custom-control-label {
    &::before {
      border-color: $gray-secondary;
      border-radius: 50%;
      width: remCalc(24);
      height: remCalc(24);
    }
    &::after {
      width: remCalc(24);
      height: remCalc(24);
    }
    &:hover {
      cursor: pointer;
    }
  }
  .custom-control-input:focus ~ .custom-control-label {
    &::before {
      border-color: $gray-line;
    }
  }
  .custom-control-input:checked ~ .custom-control-label {
    &::before {
      background-color: $red-coral;
      border-color: $red-coral;
      border-radius: 50%;
    }
    &::after {
      @include getIcon("checkmarkSmall", $white, 100);
      background-position: 50% 50%;
      background-size: contain;
      width: remCalc(24);
      height: remCalc(24);
    }
  }
}