.text-16 {
  font-size: $text-16;
}
.text-14 {
  font-size: $text-14;
}
.text-12 {
  font-size: $text-12;
}
.text-10 {
  font-size: $text-10;
}

.teaserCategoryLabel {
  letter-spacing: 2px;
}

.white-links {
  a{
    color:$white;

    &:hover{
      color:$white;
    }
  }
}

.black-links {
  a {
    &:not(.btn) {
      color: $gray-primary;
      &:hover {
        color: $gray-primary;
      }
    }
  }
}

.secondary-link {
  color: $secondary;
  text-decoration: none;
  &:hover,
  &.disabled,
  &:disabled,
  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    text-decoration: underline;
    color: $secondary;
  }
}