@include media-breakpoint-up(lg) {
  .container {
    padding-left: 0;
    padding-right: 0;
  }
}

@include media-breakpoint-down(md) {
  .container {
    max-width: none;
  }
}

@include media-breakpoint-only(md) {
  .container {
    padding-left: 32px;
    padding-right: 32px;
  }
}

// Behebt Verschiebe Bug beim ffnen von Modalen
.modal-open {
  padding-right: 0px !important;
  overflow: auto;
}

.modal {
  overflow-y: auto;
}

.modal-open[style] {
  padding-right: 0px !important;
}