// buttons

// inline buttons with min-width 160px
// overwrite this with .btn-block
.btn:not(.btn-block) {
  min-width: 10rem;
  max-width: 100%;
  @include media-breakpoint-up(md) {
    display: inline-block;
  }
}

// for btn height see Bootstrap-class --> .btn-sm + .btn-lg
.btn-small {
  @extend .display-29;
}
.btn-large {
  @extend .display-9;
}

$icon-width: 24;

.btn.icon-svg {
  &::before {
    content: '';
    position: relative;
    display: inline-block;
    left: 0;
    top: 0;
    width: #{$icon-width}px;
    height: #{$icon-width}px;
    vertical-align: bottom;
  }
  &.icon-left {
    text-indent: -#{$icon-width}px;
    &::before {
      float: left;
    }
  }
  &.icon-right {
    text-indent: #{$icon-width}px;
    &::before {
      float: right;
    }
  }
  &.icon-inline {
    &.icon-left,
    &.icon-right {
      text-indent: 0;
      &::before {
        float: none; // todo: cx-211 inline right --> ::after
        margin-right: remCalc(8);
      }
    }
  }
}

// text-Links
// Style anchor elements in paragraphs
.text-link {
  color: $dark;
  text-decoration: underline;
  &:hover,
  &.disabled,
  &:disabled,
  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    text-decoration: none;
    color: $dark;
  }
}


// textlinks (als Button, nicht inline-Text)
.btn-link.icon-svg {
  color: $dark;
  text-decoration: underline;
  $fill-color: $dark;
  $icon-map: getButtonIconUrlMap($fill-color,$icon-width);
  @each $key, $bgImageUrl in $icon-map {
    &.icon-#{$key}::before {
      background-image: $bgImageUrl;
    }
  }
  &:hover {
    text-decoration: none;
    color: $dark;
  }
  &.disabled,
  &:disabled {
    text-decoration: none;
    color: $gray-light;
    $icon-map: getButtonIconUrlMap($fill-color,$icon-width);
    @each $key, $bgImageUrl in $icon-map {
      &.icon-#{$key}::before {
        background-image: $bgImageUrl;
      }
    }
  }
  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    text-decoration: none;
  }
}

